"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";

export default function TasksPage() {
  const router = useRouter();
  const [user, setUser] = useState(null);
  const [error, setError] = useState(null);

  useEffect(() => {
    // Check if the user is logged in by calling the checkAuth API
    const checkAuthStatus = async () => {
      try {
        const res = await fetch("http://localhost:5001/api/users/check-auth", {
          method: "GET",
          credentials: "include", // Ensure cookies are sent with the request
        });

        const data = await res.json();

        if (res.ok) {
          setUser(data); // If the user is authenticated, store user data
        } else {
          setError(data.message || "Unauthorized");
          router.push("/login"); // Redirect to login page if not authenticated
        }
      } catch (err) {
        console.error("Error checking auth status", err);
        setError("Something went wrong.");
        router.push("/login"); // Redirect to login page if an error occurs
      }
    };

    checkAuthStatus();
  }, [router]);

  if (error) {
    return <div>{error}</div>; // Handle error message
  }

  return (
    <div>
      {user ? (
        <>
          <h1>Welcome, {user.username}</h1>
          {/* Your task management UI goes here */}
          <Button onClick={() => alert("Create new task")}>Create Task</Button>
        </>
      ) : (
        <p>Loading...</p>
      )}
    </div>
  );
}
